@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@300;400;500;600&display=swap');

:root {
	--animation-curve: cubic-bezier(1, 0, 0, 1);
	
  }

html{
	background-color: black;
}

label{
	width:2.667vw;
	height: 0.666vh;
	min-width: 36px;
	min-height: 5px;
	background-color: white;
	position:absolute;
	right: 24px;
	top: 48px;
	cursor: pointer;
}

label::before, label::after{
	content: "";
    width: 2.667vw;
    height: 0.666vh;
	min-width: 36px;
	min-height: 5px;
    background-color: white;
    position: absolute;
    display: block;
	transition: all 0.6s var(--animation-curve);
}

label::before{
	top: -250%;
	left: 0%;
	transform-origin: left top;
	transform:rotateZ(0deg);
}

label::after{
	bottom: -250%;
	left: 0%;
	transform-origin: left bottom;
	transform:rotateZ(0deg);
}

#toggle:checked ~ label::before {
	content: "";
	background-color: black;
	transform: rotateZ(41deg);
	left: 10%;
  }
  
#toggle:checked ~ label::after {
	content: "";
	background-color: black;
	transform: rotateZ(-41deg);
	left: 10%;
  }
  
#toggle:checked ~ label {
	background-color: black;
  }
  
#toggle ~ label {
	transition: all 0.5s var(--animation-curve);
  }
  
input {
	display: none;
  }
  

.intro_content{
	margin-left: 32px;
	height: 100vh;
	
}

.my_name{
	color: white;
	font-size: 6.67vw;
	z-index: 1;
	position: relative;
	top: 45vh;
	margin:0;
	overflow-x:hidden;
	white-space: nowrap;
	font-family: 'League Spartan', sans-serif;
	text-shadow: -0.417vw 0.0521vw rgb(105, 105, 105);
}

.my_name:hover{
	cursor: default;
}